/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
.site-header.journal {
    background-color: transparent;
    text-shadow: 1px 1px 2px #323232;
    padding-bottom: 40px;
}

.site-header{
    display: flex;
    justify-content: center;
    padding: .5rem 0;
    transition: top .5s linear;
    width: 100%;
    z-index: 3;
    border-bottom: 1px solid #e1e1e1;
    position: absolute;

    h1 {
        background: url(../../project-04/images/logos/inhabitent-logo-tent.svg);
        background-size: 50px 33px;
        height: 33px;
        margin: 0;
        overflow: hidden;
        text-indent: 100%;
        white-space: nowrap;
        width: 50px;
        position: absolute;
        top: 5px;
    }

    a {
        color: #248a83;
        display: block;
        &:hover {
            color: #2f1339;
        }
    }
}

.page-id-107 {
    .site-header {
        border-bottom: 0px;
    }
    h1 {
        background-image: url(../../project-04/images/logos/inhabitent-logo-tent-white.svg);
    }
    a {
        color: #e1e1e1;
        display: block;
        &:hover {
            color: #f0e4d1;
        }
    }
}

.page-id-11 {
    .site-header {
        border-bottom: 0px;
    }
    h1 {
        background-image: url(../../project-04/images/logos/inhabitent-logo-tent-white.svg);
    }
    a {
        color: #e1e1e1;
        display: block;
        &:hover {
            color: #f0e4d1;
        }
    }
}

.homepage-logo {
    width: 25%;
    height: auto;
}

.main-navigation {
    display: flex;
    justify-content: flex-end;
    font-family: Novecento Sans Web,sans-serif;
    text-transform: uppercase;
    position: absolute;
    top: 10px;
    right: 80px;
}

.menu-item{
    margin: auto 10px;
}